/**
 * Âπ≥Âè∞Â≠óÊÆµÁÆ°ÁêÜÂ∑•ÂÖ∑
 *
 * Êèê‰æõÁªü‰∏ÄÁöÑÂπ≥Âè∞Â≠óÊÆµÁÆ°ÁêÜÂäüËÉΩÔºåÂåÖÊã¨È™åËØÅ„ÄÅÊ†ºÂºèÂåñ„ÄÅÈªòËÆ§ÂÄºÁ≠â
 */

import {
  Platform,
  PlatformType,
  getRequiredFields,
  getOptionalFields,
  validateRequiredFields,
  createDefaultPlatformData,
  FIELD_DISPLAY_NAMES,
  FIELD_VALIDATION_RULES,
} from "../types/PlatformStructure";

/**
 * Â≠óÊÆµÈ™åËØÅÁªìÊûúÊé•Âè£
 */
export interface FieldValidationResult {
  valid: boolean;
  errors: Record<string, string[]>;
  warnings: Record<string, string[]>;
}

/**
 * Â≠óÊÆµÊ†ºÂºèÂåñÈÄâÈ°π
 */
export interface FormatOptions {
  includeOptional?: boolean;
  excludeSystem?: boolean;
  groupByCategory?: boolean;
}

/**
 * Âπ≥Âè∞Â≠óÊÆµÁÆ°ÁêÜÂ∑•ÂÖ∑Á±ª
 */
export class PlatformFieldManager {
  /**
   * È™åËØÅÂπ≥Âè∞Êï∞ÊçÆÁöÑÊâÄÊúâÂ≠óÊÆµ
   */
  static validatePlatformData(
    data: Partial<Platform>,
    platformType: PlatformType
  ): FieldValidationResult {
    const errors: Record<string, string[]> = {};
    const warnings: Record<string, string[]> = {};

    // È™åËØÅÂøÖË¶ÅÂ≠óÊÆµ
    const requiredValidation = validateRequiredFields(data, platformType);
    if (!requiredValidation.valid) {
      requiredValidation.missingFields.forEach((field) => {
        errors[field] = [
          `${
            FIELD_DISPLAY_NAMES[field as keyof typeof FIELD_DISPLAY_NAMES]
          } ÊòØÂøÖË¶ÅÂ≠óÊÆµ`,
        ];
      });
    }

    // È™åËØÅÂ≠óÊÆµÊ†ºÂºèÂíåÁ∫¶Êùü
    Object.entries(data).forEach(([fieldName, value]) => {
      const fieldErrors = PlatformFieldManager.validateField(
        fieldName,
        value,
        platformType
      );
      if (fieldErrors.length > 0) {
        errors[fieldName] = fieldErrors;
      }

      const fieldWarnings = PlatformFieldManager.getFieldWarnings(
        fieldName,
        value,
        platformType
      );
      if (fieldWarnings.length > 0) {
        warnings[fieldName] = fieldWarnings;
      }
    });

    return {
      valid: Object.keys(errors).length === 0,
      errors,
      warnings,
    };
  }

  /**
   * È™åËØÅÂçï‰∏™Â≠óÊÆµ
   */
  static validateField(
    fieldName: string,
    value: any,
    _platformType: PlatformType
  ): string[] {
    const errors: string[] = [];
    const rules =
      FIELD_VALIDATION_RULES[fieldName as keyof typeof FIELD_VALIDATION_RULES];

    if (!rules) return errors;

    const fieldDisplayName =
      FIELD_DISPLAY_NAMES[fieldName as keyof typeof FIELD_DISPLAY_NAMES] ||
      fieldName;

    // ÂøÖË¶ÅÂ≠óÊÆµÊ£ÄÊü•
    if (
      rules.required &&
      (value === null || value === undefined || value === "")
    ) {
      errors.push(`${fieldDisplayName} ‰∏çËÉΩ‰∏∫Á©∫`);
      return errors; // Â¶ÇÊûúÂøÖË¶ÅÂ≠óÊÆµ‰∏∫Á©∫Ôºå‰∏çÁªßÁª≠ÂÖ∂‰ªñÈ™åËØÅ
    }

    // Ë∑≥ËøáÁ©∫ÂÄºÁöÑÂÖ∂‰ªñÈ™åËØÅ
    if (value === null || value === undefined || value === "") {
      return errors;
    }

    // ÁâπÊÆäÂ≠óÊÆµÈ™åËØÅ
    switch (fieldName) {
      case "symbol":
        if (typeof value === "string" && !/^[A-Z0-9]{1,10}$/.test(value)) {
          errors.push("Ë¥ßÂ∏ÅÁ¨¶Âè∑Âè™ËÉΩÂåÖÂê´Â§ßÂÜôÂ≠óÊØçÂíåÊï∞Â≠óÔºåÈïøÂ∫¶1-10‰Ωç");
        }
        break;

      case "qrCodeImageUrl":
        if (
          typeof value === "string" &&
          value &&
          !PlatformFieldManager.isValidUrl(value)
        ) {
          errors.push("ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑURLÂú∞ÂùÄ");
        }
        break;
    }

    return errors;
  }

  /**
   * Ëé∑ÂèñÂ≠óÊÆµË≠¶Âëä
   */
  static getFieldWarnings(
    fieldName: string,
    value: any,
    platformType: PlatformType
  ): string[] {
    const warnings: string[] = [];

    // Â≠òÊ¨æÂπ≥Âè∞ÁâπÊÆäË≠¶Âëä
    if (platformType === "deposit") {
      if (fieldName === "address" && typeof value === "string" && value) {
        if (value.length > 100) {
          warnings.push("Âú∞ÂùÄËæÉÈïøÔºåËØ∑Á°ÆËÆ§Ê≠£Á°ÆÊÄß");
        }
      }

      if (fieldName === "fee" && typeof value === "number" && value > 0) {
        warnings.push("Â≠òÊ¨æÂπ≥Âè∞ÈÄöÂ∏∏‰∏çÊî∂ÂèñÊâãÁª≠Ë¥π");
      }
    }

    // ÊèêÁé∞Âπ≥Âè∞ÁâπÊÆäË≠¶Âëä
    if (platformType === "withdrawal") {
      if (fieldName === "maxAmount" && !value) {
        warnings.push("Âª∫ËÆÆËÆæÁΩÆÊúÄÂ§ßÊèêÁé∞ÈáëÈ¢ù‰ª•ÊéßÂà∂È£éÈô©");
      }
    }

    return warnings;
  }

  /**
   * Ê†ºÂºèÂåñÂ≠óÊÆµÊï∞ÊçÆÁî®‰∫éÊòæÁ§∫
   */
  static formatFieldsForDisplay(
    data: Partial<Platform>,
    platformType: PlatformType,
    options: FormatOptions = {}
  ): Record<string, any> {
    const formatted: Record<string, any> = {};
    const requiredFields = getRequiredFields(platformType);
    const optionalFields = getOptionalFields(platformType);

    // Â§ÑÁêÜÂøÖË¶ÅÂ≠óÊÆµ
    requiredFields.forEach((field) => {
      if (field in data) {
        formatted[field] = PlatformFieldManager.formatFieldValue(
          field,
          data[field as keyof Platform]
        );
      }
    });

    // Â§ÑÁêÜÂèØÈÄâÂ≠óÊÆµ
    if (options.includeOptional !== false) {
      optionalFields.forEach((field) => {
        if (
          field in data &&
          data[field as keyof Platform] !== null &&
          data[field as keyof Platform] !== undefined
        ) {
          formatted[field] = PlatformFieldManager.formatFieldValue(
            field,
            data[field as keyof Platform]
          );
        }
      });
    }

    // ÊéíÈô§Á≥ªÁªüÂ≠óÊÆµ
    if (options.excludeSystem) {
      delete formatted.id;
      delete formatted.createdAt;
      delete formatted.updatedAt;
    }

    return formatted;
  }

  /**
   * Ê†ºÂºèÂåñÂçï‰∏™Â≠óÊÆµÂÄº
   */
  static formatFieldValue(fieldName: string, value: any): any {
    if (value === null || value === undefined) return null;

    switch (fieldName) {
      case "minAmount":
      case "maxAmount":
      case "fee":
        return typeof value === "number" ? value.toFixed(8) : value;

      case "isActive":
        return value ? "ÂêØÁî®" : "Á¶ÅÁî®";

      case "createdAt":
      case "updatedAt":
        return value instanceof Date ? value.toLocaleString() : value;

      default:
        return value;
    }
  }

  /**
   * ÂàõÂª∫Â≠óÊÆµÁºñËæëË°®ÂçïÁöÑÈÖçÁΩÆ
   */
  static getFieldConfig(platformType: PlatformType): Record<string, any> {
    const config: Record<string, any> = {};
    const requiredFields = getRequiredFields(platformType);
    const optionalFields = getOptionalFields(platformType);

    [...requiredFields, ...optionalFields].forEach((field) => {
      config[field] = {
        label:
          FIELD_DISPLAY_NAMES[field as keyof typeof FIELD_DISPLAY_NAMES] ||
          field,
        required: requiredFields.includes(field),
        validation:
          FIELD_VALIDATION_RULES[field as keyof typeof FIELD_VALIDATION_RULES],
        ...PlatformFieldManager.getFieldInputConfig(field),
      };
    });

    return config;
  }

  /**
   * Ëé∑ÂèñÂ≠óÊÆµËæìÂÖ•ÈÖçÁΩÆ
   */
  static getFieldInputConfig(fieldName: string): Record<string, any> {
    switch (fieldName) {
      case "name":
        return {
          type: "text",
          placeholder: "‰æãÂ¶ÇÔºöÂ∏ÅÂÆâ‰∫§ÊòìÊâÄ",
          maxLength: 255,
        };

      case "symbol":
        return {
          type: "text",
          placeholder: "‰æãÂ¶ÇÔºöUSDT, BTC",
          maxLength: 50,
          transform: "uppercase",
        };

      case "network":
        return {
          type: "text",
          placeholder: "‰æãÂ¶ÇÔºöTRC20, ERC20",
          maxLength: 50,
          transform: "uppercase",
        };

      case "minAmount":
      case "maxAmount":
      case "fee":
        return {
          type: "number",
          min: 0,
          step: 0.00000001,
        };

      case "address":
        return {
          type: "text",
          placeholder: "Êî∂Ê¨æÂú∞ÂùÄ",
          maxLength: 500,
        };

      case "qrCodeImageUrl":
        return {
          type: "url",
          placeholder: "https://example.com/qr.png",
          maxLength: 500,
        };

      case "warningText":
        return {
          type: "textarea",
          placeholder: "ÈáçË¶ÅÊèêÁ§∫‰ø°ÊÅØ",
          maxLength: 1000,
        };

      case "isActive":
        return {
          type: "boolean",
          default: true,
        };

      default:
        return {
          type: "text",
        };
    }
  }

  /**
   * Ê∏ÖÁêÜÂíåÊ†áÂáÜÂåñÂπ≥Âè∞Êï∞ÊçÆ
   */
  static sanitizePlatformData(
    data: Record<string, any>,
    platformType: PlatformType
  ): Partial<Platform> {
    const sanitized: Record<string, any> = {};
    const allFields = [
      ...getRequiredFields(platformType),
      ...getOptionalFields(platformType),
    ];

    allFields.forEach((field) => {
      if (field in data) {
        sanitized[field] = PlatformFieldManager.sanitizeFieldValue(
          field,
          data[field]
        );
      }
    });

    return sanitized as Partial<Platform>;
  }

  /**
   * Ê∏ÖÁêÜÂçï‰∏™Â≠óÊÆµÂÄº
   */
  static sanitizeFieldValue(fieldName: string, value: any): any {
    if (value === null || value === undefined || value === "") return null;

    switch (fieldName) {
      case "name":
      case "address":
      case "warningText":
        return typeof value === "string" ? value.trim() : value;

      case "symbol":
      case "network":
        return typeof value === "string" ? value.trim().toUpperCase() : value;

      case "minAmount":
      case "maxAmount":
      case "fee":
        const num = parseFloat(value);
        return isNaN(num) ? null : Math.max(0, num);

      case "isActive":
        return Boolean(value);

      default:
        return value;
    }
  }

  /**
   * ÊØîËæÉ‰∏§‰∏™Âπ≥Âè∞Êï∞ÊçÆÁöÑÂ∑ÆÂºÇ
   */
  static compareData(
    oldData: Partial<Platform>,
    newData: Partial<Platform>
  ): Record<string, { old: any; new: any }> {
    const changes: Record<string, { old: any; new: any }> = {};
    const allKeys = new Set([...Object.keys(oldData), ...Object.keys(newData)]);

    allKeys.forEach((key) => {
      const oldValue = oldData[key as keyof Platform];
      const newValue = newData[key as keyof Platform];

      if (oldValue !== newValue) {
        changes[key] = { old: oldValue, new: newValue };
      }
    });

    return changes;
  }

  /**
   * È™åËØÅURLÊ†ºÂºè
   */
  static isValidUrl(string: string): boolean {
    try {
      const url = new URL(string);
      return Boolean(url);
    } catch {
      return false;
    }
  }
}

/**
 * ‰æøÊç∑ÂáΩÊï∞ÔºöÂàõÂª∫Êñ∞ÁöÑÂπ≥Âè∞Êï∞ÊçÆ
 */
export function createNewPlatform(
  platformType: PlatformType,
  initialData: Partial<Platform> = {}
): Partial<Platform> {
  const defaultData = createDefaultPlatformData(platformType);
  return { ...defaultData, ...initialData };
}

/**
 * ‰æøÊç∑ÂáΩÊï∞ÔºöÈ™åËØÅÂπ≥Âè∞Êï∞ÊçÆ
 */
export function validatePlatform(
  data: Partial<Platform>,
  platformType: PlatformType
): FieldValidationResult {
  return PlatformFieldManager.validatePlatformData(data, platformType);
}

/**
 * ‰æøÊç∑ÂáΩÊï∞ÔºöËé∑ÂèñÂ≠óÊÆµÊòæÁ§∫ÂêçÁß∞
 */
export function getFieldDisplayName(fieldName: string): string {
  return (
    FIELD_DISPLAY_NAMES[fieldName as keyof typeof FIELD_DISPLAY_NAMES] ||
    fieldName
  );
}

/**
 * ‰æøÊç∑ÂáΩÊï∞ÔºöÊ†ºÂºèÂåñÂπ≥Âè∞Êï∞ÊçÆÁî®‰∫éË°®Âçï
 */
export function formatForForm(
  data: Partial<Platform>,
  platformType: PlatformType
): Record<string, any> {
  return PlatformFieldManager.formatFieldsForDisplay(data, platformType, {
    includeOptional: true,
    excludeSystem: true,
  });
}

/**
 * ‰æøÊç∑ÂáΩÊï∞ÔºöÊ∏ÖÁêÜË°®ÂçïÊï∞ÊçÆ
 */
export function sanitizeFormData(
  formData: Record<string, any>,
  platformType: PlatformType
): Partial<Platform> {
  return PlatformFieldManager.sanitizePlatformData(formData, platformType);
}
